$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Video Player
.wp-block-vpb-video {
	* {
		box-sizing: border-box;
	}

	.vpbVideoPlayer {
		overflow: hidden;
		margin: 0 auto;
		max-width: 100%;

		.videoWrapper {
			button {
				color: #fff !important;
			}
		}

		.plyr {
			input[type=range]::-webkit-slider-runnable-track {
				box-shadow: none;
			}

			input[type=range]::-moz-range-track {
				box-shadow: none;
			}

			input[type=range]::-ms-track {
				box-shadow: none;
			}
		}
	}
}